import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup, ReactiveFormsModule } from '@angular/forms';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  chucks: any = [];
  leChuckDeResponse: any = [];
  url = "";
  categories:any=[];
  chucksDuFutur = [{"categories":[],"created_at":"2020-01-05 13:42:24.142371","icon_url":"https://assets.chucknorris.host/img/avatar/chuck-norris.png","id":"-Rf-xEIhT_2B26Fy214jqg","updated_at":"2020-01-05 13:42:24.142371","url":"https://api.chucknorris.io/jokes/-Rf-xEIhT_2B26Fy214jqg","value":"Chuck Norris invented Thomas Edison"}, {"categories":[],"created_at":"2020-01-05 13:42:25.352697","icon_url":"https://assets.chucknorris.host/img/avatar/chuck-norris.png","id":"0cwF744gTGCi8ffevHJ4BA","updated_at":"2020-01-05 13:42:25.352697","url":"https://api.chucknorris.io/jokes/0cwF744gTGCi8ffevHJ4BA","value":"your life will flash before your eyes. that means Chuck Norris has roundhoused you"}];
  myForm!: FormGroup;

  ngOnInit(): void {
    this.myForm = this.fb.group({
      searchTerm: [],

      });
  }

  showChucks(){
    this.chucks = this.chucksDuFutur;
  }
  hideChucks(){
    this.chucks=null;
  }
  showChucksApi(){
    this.http.get('https://api.chucknorris.io/jokes/random').subscribe(leChuckDeResponse=>{this.chucks.push(leChuckDeResponse)});
    console.log(this.chucks);
  }
  suppChuckApi(chuck: Object){

    let index = this.chucks.indexOf(chuck);
    this.chucks.splice(index, 1);

  }
  getCategories(){
    this.url= 'https://api.chucknorris.io/jokes/categories';
    this.http.get(this.url).subscribe(leCategoryDeResponse=>{this.categories = leCategoryDeResponse});

  }
  getJokeByCategory(category:string){
    this.url = 'https://api.chucknorris.io/jokes/random?category='+category;
    this.http.get(this.url).subscribe(leChuckDeResponse=>{this.chucks.push(leChuckDeResponse)});

  }
  search(){
    console.log(this.myForm.value.searchTerm);
    this.url = 'https://api.chucknorris.io/jokes/search?query='+this.myForm.value.searchTerm;

    this.http.get(this.url).subscribe((objetJokes:any)=>{

      this.chucks= objetJokes.result;
    });

  }

  constructor(private http: HttpClient, private fb: FormBuilder) { }
}


